body {
  background: url(./Componenets/Imgs/food.jpg);
  background-position: center center;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-size: cover;
  background-color: #535353
}

.card-bg {
  background: #e6e4e4;
}
/* .card-info{
  min-height: 28.25rem;
} */
.card-header-bg {

  background: #d1d0d0
}
.border-success-dark {
  background-color: rgb(32, 99, 32)
}

.vertical-center {
  height:100%;
  width:100%;
  min-height: 100%;  /* Fallback for browsers do NOT support vh unit */
  min-height: 100vh; /* These two lines are counted as one :-)       */

  display: flex;
  align-items: center;
  z-index: -1;
}

.infoDiv {
  position: relative;
}

.info {
  width: 100%;
  text-align: start;
  line-height: 0;
  position: relative;
  top: 10px;
  left: 10%;
  height: 0;
  font-size: 0.65rem;
  opacity: 0;
  transition: all 0.3s
}
.info-text {
  line-height: 1.75;
}

.visible {
  opacity: 1;
}

input, select {
  border: none;
  border-bottom: 1px solid #ffc185;
  background: #e6e4e4;
  width: 80%
}

